@import 'vars';
@import 'fonts';
@import 'reset';
@import 'global';
@import 'libs';
@import './blocks/blocks';

//main
.header{
   user-select: none;
   background-color: $headerWhite;
   box-shadow: 0px 4px 10px 0px $iconGray;
   margin-bottom: 25px;
   padding: 10px 0px;
   &__firstLine{
      @include f(space-between, center);
      margin-bottom: 20px;
   }
   &__location{
      text-decoration: underline dotted;
      text-underline-offset: 5px;
      display: inline-flex;
      align-items: center;
      &::before {
         content: url("../images/icons/metka.svg");
         display: inline-block;
         width: 20px;
         height: 20px;
         background-size: contain;
         background-repeat: no-repeat;
         background-position: center;
         margin-bottom: -5px;
       }
      
   }
   &__about{
      margin-left: 80px;
      a{
         margin-right: 40px;
      }
      &:last-child{
         margin-right: 0px
      }
   }

   &__secondLine{
      @include f(space-between, center);
      margin-bottom: 25px;
   }
   &__logo{
      height: 32px;
      object-fit: contain;
   }
   
   &__auth{
      text-wrap: nowrap;
      a{
         text-decoration: underline dotted;
         text-underline-offset: 15px;
      }
      &-slash{
         margin: 0px 10px;
         color: rgb(162, 163, 172);
         font-size: 12px;
      }
   }
   &__search{
      width: 100%;
      padding: 10px;
      padding-left: 15px;
      border: 1px solid rgb(162, 163, 172);
      border-radius: 5px;
      &-box{
         position: relative;
         width: 100%;
         margin: 0px 20px;
   
      }
      &::placeholder{
         font-size: 14px;
         color: rgb(162, 163, 172);
      }
      &-iconBox{
         cursor: pointer;
         position: absolute;
         top: 2.5px;
         right: 2.5px;
         background-color: $blue;
         height: 34px;
         width: 40px;
         border-radius: 5px;
         @include f(center, center);

      }
   }

   &__nav{
      @include f(space-between, center);
   }
   &__nav-button{
      background-color: $backGray;
      padding: 10px 15px;
      border-radius: 5px;
      border: none;
      &_blue{
         background-color: $blue;
         color: white;
         &_active{
            background-color: transparent;
            color: $blue;
            border: 1px solid $blue;
            box-sizing: border-box;
         }
      }
      &_orange{
         background-color: $orange;
         color: white;
         &_active{
            background-color: transparent;
            color: $orange;
            border: 1px solid $orange;
            box-sizing: border-box;
         }
      }
      &_gray_active{
         background-color: transparent;
         border: 2px solid $backGray;
      }
   }

}

.breadCrambs {
   @include f(start, center);
   margin: 20px 0px 50px 0px;
   font-weight: 300;
   &__link {
      font-weight: 300;
   }
   &__span {
      margin: 0px 20px;
   }
}

.footer {
   user-select: none;
   background-color: $footerBlue;
   color: white;
   padding: 50px 0px 15px 0px;
   font-size: 14px;
   a{
      color: white;
      font-size: 14px;
   }
   hr{
      width: 100%;
      border: 1px solid rgb(43, 45, 59);
   }
   &__content {
      @include f(space-between, start);
      margin-bottom: 50px;
   }
   &__contactUs {
      @include f(start, center);
      flex-direction: column;
   }
   &__contactUs-text{
      font-family: "Raleway", sans-serif;
      font-size: 18px;
      margin-bottom: 15px;
   }
   &__contactUs-btn {
      background-color: $blue;
      padding: 8px 15px;
      border-radius: 25px;
      border: none;
      font-size: 12px;
   }
   &__item {
      @include f(start, start);
      flex-direction: column;
      gap: 20px;
      &_apps{
         :first-child{
            margin-right: 30px;
         }
      }
      :first-child{
         margin-bottom: -5px;
      }
   }
   &__buttons-box {
      width: 234.75px;
      max-height: 100.94px;
      display: grid;
      grid-template-columns: 57% 43%;
      grid-template-rows: 1fr 1fr;
      gap: 10px;
      box-sizing: content-box;
      :first-child{
         margin-bottom: 0px;
         margin-right: 0px;
      }
   }
   &__button {
      background-color: rgb(76, 79, 95);
      border-radius: 10px;
      padding: 5px 10px;
      height: auto;
      @include f(center, center);
      &_big{
         padding: 10px 10px;
         grid-row: 1/3;
         grid-column: 2/3;
      }
   }
   &__hr{
      display: block;
      margin-bottom: 20px;
   }
   &__underLine {
      @include f(space-between, start);
      color: rgb(99, 101, 111);
      a{
         color: rgb(99, 101, 111);
         text-decoration: underline;
      }
   }
   &__item {
      &_underLine{
         gap: 5px;
      }
   }
   &__bigLetter{
      font-size: 16px;
      vertical-align: top;
   }
   &__social {
      @include f(start, start);
      :last-child{
         margin-right: 0px;
      }
   }
   &__social-btn {
      @include f(center, center);
      background-color: white;
      border-radius: 50%;
      height: 45px;
      width: 45px;
      margin-right: 15px;
   }
}

//containers
.content {
   margin: 30px 0px 100px 0px;   
   &__container{
      margin: 0px 5px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-gap: 35px;
      align-items: start;
   }
   &__title-box {
      @include f(space-between, center);
      margin-bottom: 30px;
   }
   &__title {
      font-size: 28px;
   }
   &__city {
      color: $orange;
   }
   &__count {
      font-family: "Raleway", sans-serif;
      margin-left: 30px;
      font-size: 16px;
      color: $iconGray;
   }
   &__viewMapBtn {
      background-color: $blue;
      border-radius: 5px;
      color: white;
      padding: 10px 20px;
      font-size: 14px
   }
}

.singleCard {
   width: 1110px;
   &__firstLine {
      @include f(space-between, start);
      width: 100%;
      margin-bottom: 20px;
   }
   &__firstLine-textBox {

   }
   &__title {
      font-size: 20px;
      margin-bottom: 5px;
   }
   &__text-box {
   }
   &__text {
      font-weight: 500px;
      font-size: 14px;
      font-weight: 500;
      &_gray {
         color: $iconGray;
         display: inline;
      }
      &_p {
         max-width: 500px;
      }
   }
   &__firstLine-btnBox {
         @include f(start, start);
   }
   &__button {
      height: 50px;
      width: 50px;
      border-radius: 15px;
      box-shadow: 1px 3px 8px 0px $iconGray, -1px 3px 8px 0px $iconGray;
      margin-right: 10px;
      @include f(center, center);
      &_big {
         width: auto;
         height: auto;
         padding: 16px 25px;
         margin-right: 0;
         background-color: $orange;
      }
   }
   &__secondLine {
      @include f(space-between, start);
      margin-bottom: 20px;
   }
   &__link {
      text-decoration: underline dotted;
      text-underline-offset: 5px;
   }
   &__img-box {
      width: 100%;
      background-color: $darkGray;
      height: 400px;
      border-radius: 16px;
      position: relative;
      margin-bottom: 20px;
   }
   &__stock {
      z-index: 2;
      position: absolute;
      top: 20px;
      left: 20px;
      background-color: $orange;
      padding: 10px;
      border-radius: 10px;
      color: white;
      border: none;
      box-shadow: none;
      font-size: 12px;
   }
   &__img {
      width: 100%;
      border-radius: 16px;
      height: 100%;
      object-fit: cover;
   }
   &__widgets {
      width: 1080px;
      display: grid;
      grid-template-columns: 70% 30%;
      grid-template-rows: 1fr 1fr;
      gap: 30px;
      position: relative;
      margin-bottom: 50px;
      &:first-child{
         max-height: 150px;
      }

   }
   &__widgetsRight {
      grid-row: 1/3;
      grid-column: 2/3;
      :last-child{
         //margin-bottom: -30px;
      }

   }
}

.comments {
   margin-bottom: 200px;
   &__firstLine {
      @include f(space-between, center);
      margin-bottom: 20px;
      div{
         span{
            color: $iconGray;     
            font-size: 12px;    
         }
      }
   }
   &__title {
      font-size: 16px;
      font-weight: 500;
      margin-bottom: 10px;
   }
   &__button {
      color: white;
      padding: 10px 40px;
      border-radius: 10px;
      background-color: $orange;
   }
   &__category-box {
      @include f(start, start);
      margin-bottom: 20px;
   }
   &__categotyBtn {
      background-color: rgb(237, 237, 237);
      border-radius: 10px;
      padding: 15px 20px;
      font-weight: 500;
   }
   &__content {
   }
}

//pages
.login{
   margin: 40vh auto 0px auto;
   transform: translateY(-50%);
   background-color: white;
   box-shadow: 1px 3px 8px 0px $iconGray, -1px 3px 8px 0px $iconGray;
   padding: 15px;
   border-radius: 10px;
   position: relative;
   &__prompt{
      font-size: 28px;
      text-align: center;
   }
   &__form{
   padding: 0 3%;
   }
   &__title{
      cursor: pointer;
      user-select: none;
      text-align: center;
      -webkit-tap-highlight-color: transparent;
      span{
      transition: ease 0.5s;
         font-size: 16px;
         &.active{
         transition: ease 0.5s;
            font-size: 42px;
         }
         &.login__title_slash{
            font-size: 28px;
         }
      }
   }
   &__btn{
      display: block;
      margin: 0 auto;
      padding: 10px 25px;
      margin-top: 15px;
      font-size: 14px;
      color: white;
      border-radius: 10px;
      background-color: $orange;
      cursor: pointer;
      position: relative;
      &:hover{
         transition: ease-out 0.3s;
         transform: scale(1.1);
      }
      &:before {
         content: '';
         position: absolute;
         right: -35px;
         top: 50%;
         margin-top: -12px;
         width: 24px;
         height: 24px;
         border: 2px solid var(--icons-color);
         border-left-color: transparent;
         border-right-color: transparent;
         border-radius: 50%;
         opacity: 0;
         transition: opacity 0.5s;
         animation: 0.8s linear infinite rotate;
       }
       &_sending{
         pointer-events: none;
         cursor: not-allowed;
         &:before {
            transition-duration: 1s;
            opacity: 1;
          } 
      }     
      @keyframes rotate {
         0% {
           transform: rotate(0deg);
         }
         100% {
           transform: rotate(360deg);
         }
       }
   }
}

@import 'media';
